import "./Navigation.css";
import Logo from "./logo.png";
import LogoText from "./Tanisha logo text.jpg";
import Customer from "./customer.gif";
import Document from "./document.gif";
import Social from "./social-media.gif";
import Wave from "./wave.gif";
import { Link } from 'react-router-dom';

function Navigation() {
    return (
        <div className="Navigation">
            <div >
                <Link className="logo-container" to="/">
                    <img className="logo" src={Logo} alt="logo"></img>
                    <img className="logotext" src={LogoText} alt="logo with text"></img>
                </Link>
            </div>
            {/* <img className="menu-icon" id="menu-icon" src="menu-burger.svg" onclick="openNav( )"></img> */}
            <div className="menu-container">
                <div className="menu-items">
                    <img src={Wave} alt="Wave for About" className="About"></img>
                    <Link to="/about">
                        <p className="item">About</p>
                    </Link>
                </div>
                <div className="menu-items">
                    <img src={Document} alt="Document for Projects" className="Projects"></img>
                    <Link to="/projects">
                        <p className="item">Projects</p>
                    </Link>
                </div>
                <div className="menu-items">
                    <img src={Customer} alt="Customer for Community" className="Community"></img>
                    <Link to="/community">
                        <p className="item">Community</p>
                    </Link>
                </div>
                <div className="menu-items">
                    <img src={Social} alt="Social for Contact" className="Contact"></img>
                    <Link to="/contact">
                        <p className="item">Contact Me</p>
                    </Link>
                </div>
            </div>
        </div>

    )

}

export default Navigation;